<template>
  <v-layout>
    <v-container>
      <v-row>
        <v-col>
          <add-camping
            v-on:getVal="getVal"
            v-if="isReady"
            :allProject="allProject"
            :allSection="allSection"
          />
          <center>
            <v-img
              v-if="!isGetData && !data.length"
              class="image-not-fond"
              contain
              :src="'/nodata.png'"
            >
            </v-img>
            <v-progress-circular
              id="progress"
              v-if="isGetData === true"
              :size="70"
              width="7"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </center>
        </v-col>
      </v-row>

      <v-row>
        <car-camping
          v-on:getVal="getVal"
          :allProjectData="allProject"
          v-for="(camping, index) in data"
          :key="index"
          :campingData="camping"
        />
      </v-row>
    </v-container>
  </v-layout>
</template>
<script>
import { campingController } from "../../components/camping/camping";
import { sectionController } from "../../components/section/section";
import { projectController } from "../../components/projects/project";
export default {
  async mounted() {
    this.getCampings();
    await this.getSection();
    await this.getProjects();
    this.isReady = true;
  },
  components: {
    "add-camping": () => import("../../components/camping/add.vue"),
    "car-camping": () => import("../../components/camping/card.vue"),
  },
  data() {
    return {
      isReady: false,
      isLoadProject: true,
      isLoadSection: true,
      allProject: [],
      allSection: [],
      isGetData: false,
    };
  },
  computed: {
    data() {
      return this.$store.getters.campings;
    },
  },
  methods: {
    getCampings: async function () {
      this.isGetData = true;
      await campingController.getCampings();
      this.isGetData = false;
    },
    getSection: async function () {
      var data = await sectionController.getSections();
      // this.$store.commit("SET_SECTIONS", data.campings);
      this.$store.commit("SET_SECTIONS", data);
      this.allSection = data;
    },
    getVal() {
      this.$store.commit("SET_CAMPINGS", []);
      this.getCampings();
    },
    getProjects: async function () {
      await projectController.getProjects();
    },
  },
};
</script>

<style>
#progress {
  margin-top: 20%;
}
.image-not-fond {
  margin-top: 5%;
  height: 500px;
  width: 500px;
}
</style>