import axios from "axios";
import store from "../../store/index";
export const campingController = {
    getCampings: async function () {
        var data = {
            campings: [],
            current_page: 1,
            last_page: 1,
        };
        var mainUrl = store.state.mainUrl;
        await axios.request({
            method: "GET",
            url: mainUrl + "/campings",
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                var campings = res.data.data;
                data.campings = campings;
                // console.log(store.getters.campings);
                store.commit("SET_CAMPINGS", campings);
                data.current_page = res.data.data.current_page;
                data.last_page = res.data.data.last_page;
            }
        }).catch(error => {
            this.ShowAlert('red', 'An ERROR ' + error)
        })
        return data;
    },
    addCamping: async function (file, camping) {
        console.log(camping.status);
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("total_amount", camping.total_amount);
        formData.append("status", camping.status ? 1 : 0);
        if (camping.project_id) {
            formData.append("project_id", camping.project_id);
        }
        if (camping.vedio_url) {
            formData.append("vedio_url", camping.vedio_url);
        }
        if (camping.section_id) {
            formData.append("section_id", camping.section_id);
        }
        formData.append("title[en]", camping.title.en);
        formData.append("title[ar]", camping.title.ar);
        formData.append("title[tr]", camping.title.tr);
        formData.append("description[en]", camping.description.en);
        formData.append("description[ar]", camping.description.ar);
        formData.append("description[tr]", camping.description.tr);
        formData.append("nation_id", camping.nation_id);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/campings",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                var status = !!(res.data.errors);
                if (status) {
                    state = false;
                    console.log(res.data);
                    this.ShowAlert('red', + res.data.errors);
                }
                if (res.status == 200) {
                    if (res.data.state) {
                        state = true;
                        this.ShowAlert('secondary', 'success add camping ')
                    } else {
                        this.ShowAlert('red ', 'Ann Error ')
                    }
                }
            }).catch(error => {
                console.log(error.response);
                state = false;
                console.log(error.response)
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })

        return state;
    },
    updateCamping: async function (file, camping) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("total_amount", camping.total_amount);
        formData.append("image", file);
        formData.append("status", camping.status ? 1 : 0);
        if (camping.project_id) {
            formData.append("project_id", camping.project_id);
        }
        if (camping.section_id) {
            formData.append("section_id", camping.section_id);
        }
        if (camping.vedio_url) {
            formData.append("vedio_url", camping.vedio_url);
        }
        formData.append("title[en]", camping.title.en);
        formData.append("title[ar]", camping.title.ar);
        formData.append("title[tr]", camping.title.tr);
        formData.append("description[en]", camping.description.en);
        formData.append("description[ar]", camping.description.ar);
        formData.append("description[tr]", camping.description.tr);
        formData.append("nation_id", camping.nation_id);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/campings/" + camping.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                console.log('printing in add camoing in camping js => response is ');
                console.log(res.data);
                var status = !!(res.data.errors);
                if (status) {
                    state = false;
                    this.ShowAlert('red', 'An Error ' + res.data.errors.name);
                } else {
                    state = true;
                    this.ShowAlert('secondary', 'success add camping ')
                }
            }).catch(error => {
                state = false;
                console.log(error.response)
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })

        return state;
    },


    deleteCamping: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/campings/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },

    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }



}