import axios from "axios";
import store from '../../store/index';
import { campingController } from '../camping/camping'
import { officeController } from '../offices/office'
import { sectionController } from '../section/section'
export const projectController = {
    initial: async function () {
        if (!store.getters.campings[0]) {
            // if not found camping get camping from api 
            await campingController.getCampings();
        }
        if (!store.getters.areas[0]) {
            // if not found Areas get Area  from api 
            await officeController.getAreas();
        }
        if (!store.getters.sections[0]) {
            // if not found section get section  from api 
            await sectionController.getSections();
        }



    },
    getProjects: async function () {
        console.log('get projects now .... .');
        var data = [];
        await axios.get(store.state.mainUrl + '/projects').then(response => {
            if (response.status == 200) {
                data = response.data.data;
                store.commit("SET_PROJECTS", data);
            }
        }).catch(function (error) {
            store.dispatch('showAlert', {
                color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                    + error
            })
        }
        );

        return data;
    },
    addProject: async function (file, project, areas, camping_id, section_id, video) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("areas", areas);
        formData.append("total_amount", project.total_amount);
        formData.append("description[en]", project.description.en);
        formData.append("description[ar]", project.description.ar);
        formData.append("description[tr]", project.description.tr);
        formData.append("title[en]", project.title.en);
        formData.append("title[ar]", project.title.ar);
        formData.append("title[tr]", project.title.tr);
        if (section_id) {
            formData.append("section_id", section_id);
        }
        if (camping_id) {
            formData.append("campaign_id", camping_id);
        }
        if (video) {
            formData.append("vedio_url", video);
        }

        await axios
            .request({
                method: "POST",
                url: mainUrl + "/projects",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    if (res.data.state) {
                        state = true;
                        this.ShowAlert('secondary', 'success add section ')
                    } else {
                        this.ShowAlert('red', res.data.errors)

                    }
                }
            }).catch(error => {
                state = false;
                console.log(error.response);
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })
        return state;
    },
    updateProject: async function (file, project, areas, camping_id, section_id, video) {
        {
            var state = false;
            var mainUrl = store.state.mainUrl;
            var formData = new FormData();
            formData.append("image", file);
            formData.append("areas", areas);
            formData.append("total_amount", project.total_amount);
            formData.append("description[en]", project.description.en);
            formData.append("description[ar]", project.description.ar);
            formData.append("description[tr]", project.description.tr);
            formData.append("title[en]", project.title.en);
            formData.append("title[ar]", project.title.ar);
            formData.append("title[tr]", project.title.tr);
            if (section_id) {
                formData.append("section_id", section_id);
            }
            if (camping_id) {
                formData.append("campaign_id", camping_id);
            }
            if (video) {
                formData.append("vedio_url", video);
            }

            await axios
                .request({
                    method: "POST",
                    url: mainUrl + "/projects/" + project.id,
                    data: formData,
                    headers: this.configAxios(),
                }).then(res => {
                    console.log(res.data);
                    if (res.data.state) {
                        this.ShowAlert('secondary', 'done update success  ');
                    }
                    state = true;
                }).catch(error => {
                    console.log(error.response);
                    state = false;
                    this.ShowAlert('red', 'An Error ðŸ˜¥ please contact Us '
                        + error);
                })
            return state;
        }
    },
    deleteProject: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/projects/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },


    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}